.navbar {
    position: fixed;
    z-index: 200;
    top: 60px;
    right: 50px;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    /* opacity: 0; */
    animation: visibility 4s ease;
}
.navbar-fade {
    visibility: hidden;
    opacity: 0;
    animation: fade-visibility 6s ease;
}
.navbar-more {
    position: fixed;
    z-index: 200;
    left: 350px;
    bottom: 180px;
    list-style-type: none;
    margin: 0;
    padding: 0;
    /* opacity: 0; */
    animation: visibility 4s ease;
}

.navbar .about{
    margin-right: 70px;
    padding: 10px 50px;
    font-weight: 700;
    border-radius: 50px;
    font-size: 33px;
    color: #fff;
}

.navbar li,
.navbar-more li {
    cursor: pointer;
}

.navbar .lets{
    color: rgb(47, 8, 63);
    background-color: #fff;
    border-radius: 50px;
    padding: 15px 30px;
    font-size: 33px;
    font-weight: 700;
}

.navbar-more li{
    color: #fff;
    border: 3px solid #fff;
    border-radius: 50px;
    padding: 10px 50px;
    font-size: 33px;
}

.navbar-more li:hover{
    color: rgb(47, 8, 63);
    background-color: #fff;
}